import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';
import { FacilityBookingListComponent } from './facility-booking-list/facility-booking-list.component';
import { FacilityBookingConfirmationComponent } from './facility-booking-confirmation/facility-booking-confirmation.component';
import { FacilityBookingStatusResolverService } from './facility-booking-status-resolver.service';


const routes: Routes = [
	{ path: '', redirectTo: 'history', pathMatch: 'full'},
	{ path: 'pending', component: FacilityBookingListComponent,
		children : [{
			path         : ':type/:id',
			component    : FacilityBookingConfirmationComponent,
			canActivate: [AuthGuard],
			resolve: {
				data: FacilityBookingStatusResolverService
			}
		}]
	},
	{ path: 'confirmed', component: FacilityBookingListComponent, canActivate: [AuthGuard],
		children : [{
			path         : ':type/:id',
			component    : FacilityBookingConfirmationComponent,
			canActivate: [AuthGuard],
			resolve: {
				data: FacilityBookingStatusResolverService
			}
		}]
	},
	{ path: 'cancelled', component: FacilityBookingListComponent, canActivate: [AuthGuard],
		children : [{
			path         : ':type/:id',
			component    : FacilityBookingConfirmationComponent,
			canActivate: [AuthGuard],
			resolve: {
				data: FacilityBookingStatusResolverService
			}
		}]
	},
	{ path: 'rejected', component: FacilityBookingListComponent, canActivate: [AuthGuard],
		children : [{
			path         : ':type/:id',
			component    : FacilityBookingConfirmationComponent,
			canActivate: [AuthGuard],
			resolve: {
				data: FacilityBookingStatusResolverService
			}
		}]
	},
	{ path: 'history', component: FacilityBookingListComponent, canActivate: [AuthGuard] },
	{ path: '**', redirectTo: 'history', pathMatch: 'full'}
]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FacilityBookingStatusRoutingModule { }
