import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FacilityBookingListComponent } from '../facility-booking-list/facility-booking-list.component';
import { SessionService } from 'src/app/core/session/session.service';
import { FacilityService } from 'src/app/api/controllers/Facility';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { Subscription } from 'rxjs';

import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-facility-booking-confirmation',
  templateUrl: './facility-booking-confirmation.component.html',
  styleUrls: ['./facility-booking-confirmation.component.scss']
})
export class FacilityBookingConfirmationComponent implements OnInit {
  facility:any = {};
  isDataLoaded = false;
  statusTypeData = [];
  apiUnsubcribe:Subscription;
  constructor(
    private sharedService:SharedService,
    private activeRouter: ActivatedRoute,
    private sessionService:SessionService,
    private facilityService:FacilityService,
    private changeDetectorRef: ChangeDetectorRef,
    private lookupService: LookupService,
    private router: Router,
    private facilityBookingListComponent : FacilityBookingListComponent
  ) { }

  goBack() {
    this.facilityBookingListComponent.matDrawer.close();
    this.router.navigate(['.'], {relativeTo: this.activeRouter.parent});
  }

  approved() {
    this.facility.apartmentFacilityIsSlotBooking_List = [{
      "slotId": this.facility.slotId,
      "isSlotBooking": this.facility.isSlotBooking,
      "bookedForDate": this.facility.bookedForDate,
      "bookedFromTime": this.facility.bookedFromTime,
      "bookedToTime": this.facility.bookedToTime
    }]
    this.facility.updatedBy = parseInt(this.sessionService.userId);
    this.facility.updatedOn =  _moment().toISOString();
    let params = {
      apartmentFacilityBooking: this.facility
    }
    this.facilityService.updateApartmentFacilityBooking(params).subscribe((res:any) => {
      if(res.message){
        this.goBack();
        this.facilityBookingListComponent.getBookingList();
        this.sharedService.setAlertMessage(res.message);
      }
    },
    error => {
    });
  }

  

  ngOnInit() {
    //Open the drawer
    this.apiUnsubcribe = this.sharedService.matdraweridcast.subscribe(id => {
      if(id != null && id == this.activeRouter.params['value'].id){
        this.facilityBookingListComponent.matDrawer.open();
        //Status
        let statusParams = {
          LookupTypeId: 40
        }
        this.lookupService.getLookupValueByLookupTypeId(statusParams).subscribe((res:any) => {
          this.statusTypeData = res;
        });
        //getFacilityBookingId
        let params = {
          apartmentFacilityBookingId: parseInt(id)
        }
        this.facilityService.getApartmentFacilityBookingsByFacilityBookingId(params).subscribe((res:any) => {
          this.facility = res[0];
          this.isDataLoaded = true;
        })
      }
      //Mark for check
      this.changeDetectorRef.markForCheck();
    })
  }

  ngOnDestroy() {
    this.apiUnsubcribe.unsubscribe();
  }

}
